
#container{width: 100%; height: 100vh; position: relative;}
.header{
  width: 100%; height: 90px;
  position: absolute;
  z-index: 80;
  font-size: 14px;
}
.headLogo{
  visibility: visible;
  animation-duration: 0.8s;
  animation-delay: 0.2s;
  animation-name: fadeInDown;

  height: 90px;
  box-sizing: border-box;
  transition: all .3s ease-in-out;
  background-color: rgba(0,0,0,0);
}
.clear::after{
  display: block;
  content: "";
  clear: both;
  height: 0;
}
.headLogo .headInner{
  overflow: hidden;
  max-width: 1780px;
  padding-top: 20px;

  position: relative;
  height: 100%;
  width: 97%;
  margin: 0 auto;
}
.headLogo .headInner .logo{width: 500px; float: left;}
.headLogo .headInner .logo a{text-decoration: none; color: #000;}
.headLogo .headInner .logo a img{width: 100%;}
