@charset "utf-8";

/*side01 s*/
.menu .sec{
    margin-top: 80px;
    box-sizing: border-box;
    width: 100%;
}
.menu .sec::after{
    display: block;
    content: "";
    clear: both;
    height: 0;
}
#menu1{ 
    height: 100vh; max-width: 100%; width: 100%;
    top: 0; left: 0;
    position: absolute;
}
#menu1 .side{
    width: 100%; height: 100%;
    z-index: 102;
}
#menu1 .side .sideCon{
    width: 100%; min-height: 100vh; height: auto;
    background: #fff;
    z-index: 102;
    transition: all .3s ease-in;
}
#menu1 .side .sideCon .sideHead{
    width: 100%; height: 450px;
    background: url(../../img/menu1/side_vis01.jpg) no-repeat center/cover;
    position: relative;
    z-index: 102;
}
#menu1 .side .sideCon .sideHead .close{
    position: absolute;
    right: 0; top: 0;
    width: 70px; height: 70px;
    text-indent: -99999px;
    background: #000;
    cursor: pointer;
    z-index: 10;
}
#menu1 .side .sideCon .sideHead .close span{
    width: 38px; height: 4px;
    background-color: #fff;
    display: inline-block;
    transform: rotate(-45deg);
    position: absolute;
    top: 32px; left: 16px;
    cursor: pointer;
}
#menu1 .side .sideCon .sideHead .close span:last-child{transform: rotate(45deg);}

/* inner */
#menu1 .side .sideCon .sideHead .inner{
    position: relative;
    width: 97%; max-width: 1300px;
    margin: 0 auto;
    z-index: 1;
}
#menu1 .side .sideCon .sideHead .inner .liveBtn{width: 100%;}
#menu1 .side .sideCon .sideHead .inner .liveBtn .liveOn{
    margin-top: 65px;
    padding: 10px 42px 8px;
    text-align: center;
    display: inline-block;
    border: 3px solid #000;
    background: #e62525;
    color: #fff;
    font-size: 18px;
    box-sizing: border-box;
}
#menu1 .side .sideCon .sideHead .inner .liveBtn .liveOn a{text-decoration: none; color: #000; font-weight: bold;}
#menu1 .side .sideCon .sideHead .inner .vCount{
    margin-top: 10px;
    padding: 12px 10px 9px;
    background: #36558c;
    border: 3px solid #000;
    display: inline-block;
    color: #fff;
    font-weight: bold;
}
#menu1 .side .sideCon .sideHead .inner .vCount .vn{color: #ffd06a;}

/* mant */
#menu1 .side .sideCon .sideHead .ment{
    position: absolute;
    width: 100%; height: 180px;
    bottom: 40px; left: 0px;
}
#menu1 .side .sideCon .sideHead .ment .inner{
    position: relative;
    width: 97%; max-width: 1300px;
    margin: 0 auto;
    font-weight: bold;
}
#menu1 .side .sideCon .sideHead .ment .inner .tit{
    color: #004e55;
    display: inline-block;
    border-bottom: solid 3px #6ac0a4;
    line-height: 100%;
    position: relative;
    margin-bottom: 20px;
    font-size: 72px;
    font-weight: bolder;
}
#menu1 .side .sideCon .sideHead .ment .inner .tit::before{
    content: "";
    position: absolute;
    top: -72px; left: 0;
    background: url(../../img/menu1/sp01.png) 0 0 no-repeat;
    width: 50px; height: 50px;
}
#menu1 .side .sideCon .sideHead .ment .inner .tit::after{
    content: "";
    position: absolute;
    bottom: 27px; right: -55px;
    background: url(../../img/menu1/sp01.png) 0 -64px no-repeat;
    width: 43px; height: 50px;
}
#menu1 .side .sideCon .sideHead .ment .inner .txt{
    font-size: 18px; line-height: 140%;
}
#menu1 .side .sideCon .sideHead .ment .inner .block{
    padding-top: 6px; display: block;
}

/* navWrap */
#menu1 .side .sideCon .sideHead .navWrap{
    position: absolute;
    bottom: -40px; left: 50%;
    transform: translateX(-50%);
    width: 100%; max-width: 1300px;
    z-index: 1;
}
#menu1 .side .sideCon .sideHead .navWrap .nav{
    display: flex;
    border: 3px solid #000;
    box-sizing: border-box;
    background: #fff;
}
#menu1 .side .sideCon .sideHead .navWrap .nav li{
    flex: 1 1 auto;
    position: relative;
}
#menu1 .side .sideCon .sideHead .navWrap .nav li:first-child::after{
    display: none;
}
#menu1 .side .sideCon .sideHead .navWrap .nav li::after{
    position: absolute;
    width: 2px; height: 27px;
    background: #000;
    left: -1px; top: 50%;
    transform: translateY(-50%);
    content: "";
}
#menu1 .side .sideCon .sideHead .navWrap .nav li a{
    background-color: #fff;
    width: 100%; height: 100%;
    text-align: center;
    display: block;
    padding: 20px;
    box-sizing: border-box;
    font-weight: bold;
}
#menu1 .side .sideCon .sideHead .navWrap .nav li a.on{
    background-color: #ffd06a;
}

/* inner clear */
#menu1 .side .sideCon .inner2{
    height: auto; padding: 120px 0;
}
#menu1 .side .sideCon .inner2 .tabCon{display: none;}
#menu1 .side .sideCon .inner2 .tabCon.on{display: block;}
#menu1 .secBox {margin-top: 80px;}
#menu1 .secBox .box {
    display: inline-block; 
    width: 32%; height: 200px; 
    margin-left: 2%; float: left;
    font-weight: bold;
}
#menu1 .secBox .box:first-child, #menu1 .secBox .box:nth-child(3n+1) {margin-left: 0;}
#menu1 .secBox .box .ti {
    border: 3px solid; 
    padding: 15px; 
    transform: translateY(-3px); 
    line-height: 140%; 
    background: #6ac0a4; 
    text-align: center;
    font-weight: bolder;
}
#menu1 .secBox .box .tx {
    font-family: 'NanumSquare';font-size: 16px; 
    transform: skew(0.03deg); 
    padding-top: 20px; 
    font-weight: 400; 
    color: rgb(102, 102, 102);
    word-break: keep-all;
    font-weight: bold;
}



/* tab1 */
#tab1_1 .sec1 { 
    padding: 29px 0 25px; 
    background: #36558c; 
    text-align: center; 
    border: 3px solid; 
    position: relative;
    font-weight: bold;
}
#tab1_1 .sec1::before {
    content:""; position: absolute; 
    width: 60px; height: 70px; 
    top: 30px; left: 70px; 
    background: url("../../img/menu1/sp02.png") 0 -132px no-repeat;
}
#tab1_1 .sec1::after {
    content:""; position: absolute; 
    width: 83px; height: 97px; 
    top: -48px; right: 0; 
    background: url("../../img/menu1/sp02.png") 0 0 no-repeat;
}
#tab1_1 .sec1 .txt1 {
    color: #fff; 
    font-size: 20px; 
    letter-spacing: 7px; 
    padding-bottom: 20px;
    font-weight: bolder;
}
#tab1_1 .sec1 .txt2 {
    color: #ffd06a; 
    font-size: 42px;
    font-weight: bolder;
}
#tab1_1 .sec2 {
    border: 3px solid; 
    padding: 30px;
}
#tab1_1 .sec2 .boxL {
    width: 40%; max-width: 510px; 
    display: inline-block; 
    float: left; margin-right: 4%;
}
#tab1_1 .sec2 .boxL img {
    width: 100%; 
    border: 3px solid #000; 
    box-sizing: border-box;
}
#tab1_1 .sec2.sec3 .boxL img {border:none;}
#tab1_1 .sec2 .boxR {
    display: inline-block; 
    width: 56%; 
    font-family: 'NanumSquare'; font-size: 16px; 
    transform: skew(0.03deg);
    padding-top: 15px; font-weight: 400;
}
#tab1_1 .sec2 .boxR .info li {
    display: block; 
    width: 100%; 
    padding-bottom: 27px;
}
#tab1_1 .sec2 .boxR .info li::after {
    display: block;
    content: ""; 
    clear: both; 
    height: 0;
}
#tab1_1 .sec2 .boxR .info li > p {display: inline-block;}
#tab1_1 .sec2 .boxR .info .tit {
    width: 170px; 
    font-family: 'BMDH'; 
    font-size: 20px;
    vertical-align: top; 
    font-weight: bold;
}
#tab1_1 .sec2 .boxR .info .tit > span {
    padding: 0 15px; 
    background: #ffd06a; 
}
#tab1_1 .sec2 .boxR .info .txt {
    vertical-align: top; 
    display: inline-block; 
    width: calc(100% - 180px); 
    padding-left: 10px; float: right;
}
#tab1_1 .sec2 .boxR .txtBox .txt {
    margin-top: 25px; 
    transform:none;
}
#tab1_1 .sec2 .boxR .txtBox .txt:first-child {margin-top: 0;}

/*교육감 인사말*/
#tab1_1 .bmdh {color: #004e55; font-weight: 600;}
#tab1_1 .sec2.sec3 .boxL {width: 22%; max-width: 270px;}
#tab1_1 .sec2.sec3 .boxR {width: 74%;}
#tab1_1 .sec2.sec3 .boxR .txtBox {padding: 50px 0;}



/* tab2 */
#tab1_2 .sec1 .img {
    width: 100%; height: 100%; 
    display: block; 
    margin: 0 auto; 
    overflow: hidden;  
    border: 3px solid; 
    box-sizing: border-box;
}
#tab1_2 .sec1 .img > a {
    position: relative; 
    display: block; 
    width: 100%; height: 100%; 
    pointer-events: none; cursor: default;
}
