/*20201111 라이브 영상 썸네일*/
#menu1 .liveThumb img {width: 100%;}
#menu1 .liveThumb a {
    position: relative; 
    display: inline-block; 
    width: 100%;
}
#menu1 .liveThumb a::before {
    position: absolute; 
    content: ""; 
    width: 120px; height: 120px; 
    top: 50%; left: 50%; 
    transform: translate(-50%, -50%); 
    background: url("../../img/menu1/play_btn_b_1.png") center/contain no-repeat; z-index: 3;transition: all .3s ease-in-out;
}
#menu1 .liveThumb a::after{
    transition: all .3s ease-in-out; 
    position: absolute; 
    content: ""; 
    width:100%; height: 100%; 
    background-color:  rgba(0,0,0,0); 
    top: 50%; left: 50%; 
    transform: translate(-50%, -50%);
    z-index: 2; 
    display: inline-block;
}
#menu1 .liveThumb:hover a::before {background-image: url("../../img/menu1/play_btn_b.png");}
#menu1 .liveThumb:hover a::after {background-color: rgba(0,0,0,0.3); }
