@charset "utf-8";

/*main*/
body {
    font-family: 'BMDH', 'NanumSquare', 'NotoSans';  
    letter-spacing:-.5px; box-sizing:border-box; 
    color:#111; font-size:20px; font-weight:300; 
    background-color: #f3c866; height: 100%;
}

/*visual*/
.mainPage {overflow-y: hidden; height: 100vh;}

*{margin: 0; padding: 0; box-sizing: border-box;}
a { text-decoration:none; color:#000}
@font-face { font-family: 'BMDOHYEON'; src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_one@1.0/BMDOHYEON.woff') format('woff'); font-weight: normal; font-style: normal; }

.svgWrap { 
    width: 100%; height: 100vh; 
    min-height: 667px; overflow-y: hidden; 
    background-color: #f3c866; position:relative;
}
.svgCon {
    width: 1880.49px; height: 100%;
    min-width: 1280px; min-height: 667px;
    max-width: 1920px; max-height: 1080px;
    z-index: 1;
    margin: 0 auto;
    position: relative;
}
svg { 
    height: 100%; width: auto;
    min-width: 1280px; min-height: 667px;
    max-width: 1920px; max-height: 1080px; 
    margin: 0 auto; 
    background-color: #f3c866; display: block;
}
.btWave {
    width: 1880.49px;
    background: url("../../img/main/bottom_wave.png") repeat center; 
    position: absolute; 
    bottom: 0; left: 0; 
    width: 100%; height: 90px; 
    z-index: 10;
}
.btWave .btLogoWrap {
    transform: translateY(29px); 
    width: 97%; max-width: 1780px; 
    margin: 0 auto; font-size: 19.5px;
}
.btWave .btLogoWrap .lgw {display: inline-block;}
.btWave .btLogoWrap .lgw .btText {
    display: inline-block; 
    margin-left: 30px; 
    vertical-align: bottom; 
    margin-bottom: 3px;
    font-weight: bold;
}
.btWave .btLogoWrap .lgw .btLogo {
    display: inline-block; 
    margin-left: 30px; 
    vertical-align: bottom;
}
.btWave .btLogoWrap .lgw .btLogo img {width: 100%;}
.btWave .btLogoWrap .lgw .btLoca {
    margin-left: 37px; 
    padding-left: 40px; 
    border-left: 2px solid #000;
}

.btn { 
    text-align: center; 
    font-family:'BMDOHYEON'; font-size: 22px; 
    position: absolute; display: block;
}
.mainPage .btn.btn1 {width: 15%; height: 30%; top: 14%; left: 23%; border-radius: 49%;}
.mainPage .btn.btn1 .box { margin-top:30px;}
.mainPage .btn.btn2 {width: 12%; height: 29%; top: 37%; left: 11%; border-radius: 20%; border-bottom-right-radius: 50%; }
.mainPage .btn.btn2 .box {min-width: 210px;}
.mainPage .btn.btn3 {width: 22%; height: 29%; top: 57%; left: 22%; border-radius: 20%; border-bottom-right-radius: 50%; border-top-left-radius: 50%;}
.mainPage .btn.btn3 {width: 22%; height: 29%; top: 57%; left: 22%; border-radius: 50%; border-bottom-left-radius: 20%; }
.mainPage .btn.btn4 {width: 25%; height: 52%; top: 14%; left: 38%; border-radius: 20%; border-bottom-right-radius: 50%; border-bottom-left-radius: 50%;}
.mainPage .btn.btn5 {width: 19%; height: 44%; top: 8%;  left: 64%; border-radius: 50%;}
.mainPage .btn.btn5 .box { margin-top:130px;}
.mainPage .btn.btn6 {width: 17%; height: 25%; top: 58%; left: 60%; border-radius: 50%;}
.mainPage .btn .box {margin-top:-50px; transform:translateY(-30px);opacity:0; transition:.3s ease;}
.btn .box { display:inline-block;  background:url("../../img/main/mark_center.png") repeat-x; padding: 0px 0 15px; position:relative; text-align: center;}
.mainPage .btn:hover .box {transform:translateY(0); opacity:1;}
.btn .box::before { content:''; top:0; display:inline-block; background:url("../../img/main/mark_left.png") no-repeat; width:20px; height:43px; left:-19px; position:absolute; }
.btn .box::after { content:''; top:0; display:inline-block; background:url("../../img/main/mark_right.png") no-repeat; width:20px; height:43px; right :-19px; position:absolute; }
.btn .box .txt { padding-left:5px;vertical-align:middle; height:21px; display:inline-block; position: relative; line-height: 100%;}
.btn .box .mark {display: inline-block; content: "";background:url("../../img/main/mark_ico.png") no-repeat 1px center;  width: 20px; height: 45px; vertical-align: middle;}
.btn .box .icon { margin-left:15px; text-indent:-99999px;background:url("../../img/main/mark_ico2.png") no-repeat; display:inline-block; width:12px; height:24px; vertical-align:middle;}
.btn .box .trig { background:url("../../img/main/mark_bottom.png") no-repeat; display:inline-block; width:20px; height:20px; position:absolute; bottom:0; left:50%; transform:translateX(-50%)}
.btn .box .trig::before {content: ""; width: 22px; height: 2px; background-color: #fff; position: absolute; top: -1px; left: 50%; transform: translateX(-50%);}

html, body {height: 100%;}
#container, .contents {width: 100%; height: 100%; position: relative;}




