@charset "utf-8";

.menu .sec{
    margin-top: 80px;
    box-sizing: border-box;
    width: 100%;
}
.menu .sec:last-of-type{margin-bottom: 80px;}
.menu .sec::after{
    display: block;
    content: "";
    clear: both;
    height: 0;
}
#menu2{ 
    height: 100vh; max-width: 100%; width: 100%;
    top: 0; left: 0;
    position: absolute;
}
#menu2 .side{
    width: 100%; height: 100%;
    z-index: 102;
}
#menu2 .side .sideCon{
    width: 100%; min-height: 100vh; height: auto;
    background: #fff;
    position: absolute; top:0;
    z-index: 102;
    transition: all .3s ease-in;
}
#menu2 .side .sideCon .sideHead{
    width: 100%; height: 450px;
    background: url(../../img/menu2/side_vis02.jpg) no-repeat center/cover;
    position: relative;
}
#menu2 .side .sideCon .sideHead .close{
    position: absolute;
    right: 0; top: 0;
    width: 70px; height: 70px;
    text-indent: -99999px;
    background: #000;
    cursor: pointer;
    z-index: 10;
}
#menu2 .side .sideCon .sideHead .close span{
    width: 38px; height: 4px;
    background-color: #fff;
    display: inline-block;
    transform: rotate(-45deg);
    position: absolute;
    top: 32px; left: 16px;
    cursor: pointer;
}
#menu2 .side .sideCon .sideHead .close span:last-child{transform: rotate(45deg);}

/* head_ment */
#menu2 .side .sideCon .sideHead .ment{
    position: absolute;
    width: 100%; height: 180px;
    bottom: 40px; left: 0px;
}
#menu2 .side .sideCon .sideHead .ment .inner{
    position: relative;
    width: 97%; max-width: 1300px;
    margin: 0 auto;
    font-weight: bold;
}
#menu2 .side .sideCon .sideHead .ment .inner .tit::before{
    content: "";
    position: absolute;
    top: -72px; left: 0;
    background: url(../../img/menu4/sp01.png) 0 0 no-repeat;
    width: 50px; height: 50px;
}
#menu2 .side .sideCon .sideHead .ment .inner .tit{
    color: #004e55;
    display: inline-block;
    border-bottom: solid 3px #6ac0a4;
    line-height: 100%;
    position: relative;
    margin-bottom: 20px;
    font-size: 72px;
    font-weight: bolder;
}
#menu2 .side .sideCon .sideHead .ment .inner .tit::after{
    content: "";
    position: absolute;
    bottom: 27px; right: -55px;
    background: url(../../img/menu4/sp01.png) 0 -64px no-repeat;
    width: 43px; height: 50px;
}
#menu2 .side .sideCon .sideHead .ment .inner .brPk{
    border-color: #f58184 !important;
}

/* navWrap */
#menu2 .side .sideCon .sideHead .navWrap{
    position: absolute;
    bottom: -40px; left: 50%;
    transform: translateX(-50%);
    width: 100%; max-width: 1300px;
    z-index: 1;
}
#menu2 .side .sideCon .sideHead .navWrap .nav{
    display: flex;
    border: 3px solid #000;
    box-sizing: border-box;
    background: #fff;
}
#menu2 .side .sideCon .sideHead .navWrap .nav li{
    flex: 1 1 auto;
    position: relative;
}
#menu2 .side .sideCon .sideHead .navWrap .nav li:first-child::after{
    display: none;
}
#menu2 .side .sideCon .sideHead .navWrap .nav li::after{
    position: absolute;
    width: 2px; height: 27px;
    background: #000;
    left: -1px; top: 50%;
    transform: translateY(-50%);
    content: "";
}
#menu2 .side .sideCon .sideHead .navWrap .nav li a{
    background-color: #fff;
    width: 100%; height: 100%;
    text-align: center;
    display: block;
    padding: 20px;
    box-sizing: border-box;
    font-weight: bold;
}
#menu2 .side .sideCon .sideHead .navWrap .nav li a.on{
    background-color: #ffd06a;
}
#menu2 .side .sideCon .sideHead .navWrap .nav li a .sTit{
    font-size: 14px; font-weight: normal;
}



#menu2 .side .sideCon .inner{
    height: auto; width: 97%; max-width: 1300px;
    margin: 0 auto;
    position: relative;
}
#menu2 .side .sideCon .inner .tabCont{}
#menu2 .side .sideCon .inner .tabCont .sec{
    margin-top: 80px;
    box-sizing: border-box;
    width: 100%;
}
#menu2 .side .sideCon .inner .tabCont .sec::after{
    display: block;
    content: "";
    clear: both;
    height: 0;
}
#menu2 .side .sideCon .inner .tabCont .sec .boxL{
    display: inline-block;
    float: left;
    width: 50%;
}
#menu2 .side .sideCon .inner .tabCont .sec .boxL a::before{
    position: absolute;
    content: "";
    width: 97px; height: 97px;
    top: 50%; left: 50%;
    transform: translate(-50%, -50%);
    background: url(../../img/menu2/play_btn_1.png) center/contain;
    z-index: 3;
    transition: all .3s ease-in-out;
}
#menu2 .side .sideCon .inner .tabCont .sec .boxL a{
    position: relative;
    display: inline-block;
    width: 100%;
}
#menu2 .side .sideCon .inner .tabCont .sec .boxL a::after{
    transition: all .3s ease-in-out;
    position: absolute;
    content: "";
    width: 100%; height: 100%;
    background-color: rgba(0,0,0,0);
    top: 50%; left: 50%;
    transform: translate(-50%, -50%);
    z-index: 2;
    display: inline-block;
}
#menu2 .side .sideCon .inner .tabCont .sec .boxL a img{width: 100%;}
#menu2 .side .sideCon .inner .tabCont .sec .boxL:hover a::before {background-image: url("../../img/menu4/play_btn_b.png");}
#menu2 .side .sideCon .inner .tabCont .sec .boxL:hover a::after {background-color: rgba(0,0,0,0.3); }


#menu2 .side .sideCon .inner .tabCont .sec .boxR{
    display: inline-block;
    float: right;
    width: 46%; height: 365px;
    position: relative;
}
#menu2 .side .sideCon .inner .tabCont .sec .boxR .titBox{position: relative;}
#menu2 .side .sideCon .inner .tabCont .sec .boxR .titBox .bottom{
    border: 3px solid;
    padding: 15px;
    line-height: 140%;
    background: #6ac0a4;
    font-weight: bold;
}
#menu2 .side .sideCon .inner .tabCont .sec .boxR .txtBox .txt{
    font-family: 'NanumSquare';
    font-size: 16px;
    transform: skew(0.03deg);
    padding-top: 20px;
    font-weight: bold;
}
#menu2 .side .sideCon .inner .tabCont .sec .boxR .btnBox{
    position: absolute; bottom: 20px; width: 100%;
}
#menu2 .side .sideCon .inner .tabCont .sec .boxR .btnBox > a{
    display: inline-block;
    width: 48%; height: 65px;
    text-align: center;
    border: 3px solid;
    box-sizing: border-box;
    margin-left: 4%;
    float: right;
    position: relative;
    transition: all .2s ease-in-out;
    background: #fff;
}
#menu2 .side .sideCon .inner .tabCont .sec .boxR .btnBox .btn.off{
    cursor: default; background: #ddd;
}
#menu2 .side .sideCon .inner .tabCont .sec .boxR .btnBox .btn.off.hidden{
    display: none;
}
#menu2 .side .sideCon .inner .tabCont .sec .boxR .btnBox a:first-child{
    float: left; margin-left: 0;
}
#menu2 .side .sideCon .inner .tabCont .sec .boxR .btnBox a .btnWrap{
    position: absolute;
    top: 50%; left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    box-sizing: border-box;
    padding-right: 0;
}


