@charset "utf-8";

.menu .sec{
    margin-top: 80px;
    box-sizing: border-box;
    width: 100%;
}
.menu .sec::after{
    display: block;
    content: "";
    clear: both;
    height: 0;
}
#menu5{ 
    height: 100vh; max-width: 100%; width: 100%;
    top: 0; left: 0;
    position: absolute;
}
#menu5 .side{
    width: 100%; height: 100%;
    z-index: 102;
}
#menu5 .side .sideCon{
    width: 100%; min-height: 100vh; height: auto;
    background: #fff;
    position: absolute; top:0;
    z-index: 102;
    transition: all .3s ease-in;
}
#menu5 .side .sideCon .sideHead{
    width: 100%; height: 450px;
    background: url(../../img/menu5/side_vis05.jpg) no-repeat center/cover;
    position: relative;
}
#menu5 .side .sideCon .sideHead .close{
    position: absolute;
    right: 0; top: 0;
    width: 70px; height: 70px;
    text-indent: -99999px;
    background: #000;
    cursor: pointer;
    z-index: 10;
}
#menu5 .side .sideCon .sideHead .close span{
    width: 38px; height: 4px;
    background-color: #fff;
    display: inline-block;
    transform: rotate(-45deg);
    position: absolute;
    top: 32px; left: 16px;
    cursor: pointer;
}
#menu5 .side .sideCon .sideHead .close span:last-child{transform: rotate(45deg);}

/* head_ment */
#menu5 .side .sideCon .sideHead .ment{
    position: absolute;
    width: 100%; height: 180px;
    bottom: 40px; left: 0px;
}
#menu5 .side .sideCon .sideHead .ment .inner{
    position: relative;
    width: 97%; max-width: 1300px;
    margin: 0 auto;
    font-weight: bold;
}
#menu5 .side .sideCon .sideHead .ment .inner .tit::before{
    content: "";
    position: absolute;
    top: -72px; left: 0;
    background: url(../../img/menu4/sp01.png) 0 0 no-repeat;
    width: 50px; height: 50px;
}
#menu5 .side .sideCon .sideHead .ment .inner .tit{
    color: #004e55;
    display: inline-block;
    border-bottom: solid 3px #6ac0a4;
    line-height: 100%;
    position: relative;
    margin-bottom: 20px;
    font-size: 72px;
    font-weight: bolder;
}
#menu5 .side .sideCon .sideHead .ment .inner .tit::after{
    content: "";
    position: absolute;
    bottom: 27px; right: -55px;
    background: url(../../img/menu4/sp01.png) 0 -64px no-repeat;
    width: 43px; height: 50px;
}
#menu5 .side .sideCon .sideHead .ment .inner .brPk{
    border-color: #f58184 !important;
}

/* navWrap */
#menu5 .side .sideCon .sideHead .navWrap{
    position: absolute;
    bottom: -40px; left: 50%;
    transform: translateX(-50%);
    width: 100%; max-width: 1300px;
    z-index: 1;
}
#menu5 .side .sideCon .sideHead .navWrap .nav2{
    display: flex;
    border: 3px solid #000;
    box-sizing: border-box;
    background: #fff;
}
#menu5 .side .sideCon .sideHead .navWrap .nav2 li{
    flex: 1 1 auto;
    position: relative;
}
#menu5 .side .sideCon .sideHead .navWrap .nav2 li:first-child::after{
    display: none;
}
#menu5 .side .sideCon .sideHead .navWrap .nav2 li::after{
    position: absolute;
    width: 2px; height: 27px;
    background: #000;
    left: -1px; top: 50%;
    transform: translateY(-50%);
    content: "";
}
#menu5 .side .sideCon .sideHead .navWrap .nav2 li a{
    background-color: #fff;
    width: 100%; height: 100%;
    text-align: center;
    display: block;
    padding: 20px;
    box-sizing: border-box;
    font-weight: bold;
}
#menu5 .side .sideCon .sideHead .navWrap .nav2 li a.on{
    background-color: #ffd06a;
}

/*side05 s*/

#tab5_1 .sec .titBox {
    position: relative; 
    border: 3px solid; 
    padding: 15px; 
    line-height: 140%; 
    background: #f4c867;
    font-weight: bold;
    margin-top: 140px;
}
#tab5_1 .sec .titBox::after {
    position: absolute; 
    width: 83px; height: 97px; 
    top: -50px; right: 0; 
    background: url("../../img/menu5/sp02.png") 0 0 no-repeat; 
    content: "";
}
#tab5_1 .sec .img {
    background: url("../../img/menu5/s5_t1_01.jpg") center/cover no-repeat; 
    width: 100%; height: 510px; 
    position: relative; margin-top: 30px; 
    border: 3px solid; box-sizing: border-box;
}
#tab5_1 .sec .img img {
    position: absolute; 
    top: 50%; left: 50%; 
    transform: translate(-50%, -50%);
}
#tab5_1 .sec .btnBox {text-align: center; margin-top: 30px;}
#tab5_1 .sec .btnBox .btn { 
    display: inline-block; 
    width: 300px; height: 65px; 
    text-align: center; 
    border: 3px solid; 
    box-sizing: border-box; 
    position: relative; 
    transition: all .2s ease-in-out; 
    background: #fff;
}
#tab5_1 .sec .btnBox .btn:hover {background: #f58182;}
#tab5_1 .sec .btnBox .btn .btnWrap {
    position: absolute; 
    top: 50%; left: 50%; 
    transform: translate(-50%, -50%); 
    width: 100%; 
    box-sizing: border-box;
    padding-right: 20px; 
}
#tab5_1 .sec .btnBox .btn .btnWrap::after {
    background: url("../../img/menu5/sp02.png") 0 -232px no-repeat; 
    width:30px; height: 37px; 
    position: absolute; 
    content: "";
    right: 50px; top: 50%; 
    transform: translateY(-50%); 
}
#tab5_1 .sec2 {margin-top: 50px;}
#tab5_1 .sec2 .btn {
    display: inline-block; 
    width: 32%; 
    border: 3px solid #000; 
    padding: 15px; float: left; 
    box-sizing: border-box; 
    margin-left: 2%;  
    background: #6ac0a4; 
    position: relative;
    transition: all .2s ease; 
    text-align: left;
}
#tab5_1 .sec2 .btn::after, #tab5_1 .sec2 .btn::before {
    position: absolute; 
    content: ""; 
    right: 25px; top: 46%; 
    transform: translateY(-50%) rotate(45deg);
    width: 13px; height: 3px; 
    background: #000; 
    transition: all .2s ease;
}
#tab5_1 .sec2 .btn::before {transform: translateY(-50%) rotate(-45deg); top: 54%;}
#tab5_1 .sec2 .btn:hover {background: #f58182;}
#tab5_1 .sec2 .btn:hover::after, #tab5_1 .sec2 .btn:hover::before { right: 7px;}
#tab5_1 .sec2 .btn .txt {font-size: 14px;}
#tab5_1 .sec2 .btn .tit {padding: 10px 0 10px 40px; position: relative;}
#tab5_1 .sec2 .btn:first-child {margin: 0;}
#tab5_1 .sec2 .btn .tit::after {
    content: ""; 
    position: absolute; 
    top: -2px; left: -2px; 
    background: url("../../img/menu5/sp01.png") 0 0 no-repeat; 
    width: 36px; height: 36px; 
    background-size: 1000%;
}
