@charset "utf-8";

.menu .sec{
    margin-top: 80px;
    box-sizing: border-box;
    width: 100%;
}
.menu .sec:last-of-type{margin-bottom: 80px;}
.menu .sec::after{
    display: block;
    content: "";
    clear: both;
    height: 0;
}
#menu3{ 
    height: 100vh; max-width: 100%; width: 100%;
    top: 0; left: 0;
    position: absolute;
}
#menu3 .side{
    width: 100%; height: 100%;
    z-index: 102;
}
#menu3 .side .sideCon{
    width: 100%; min-height: 100vh; height: auto;
    background: #fff;
    position: absolute; top:0;
    z-index: 102;
    transition: all .3s ease-in;
}
#menu3 .side .sideCon .sideHead{
    width: 100%; height: 450px;
    background: url(../../img/menu3/side_vis03.jpg) no-repeat center/cover;
    position: relative;
}
#menu3 .side .sideCon .sideHead .close{
    position: absolute;
    right: 0; top: 0;
    width: 70px; height: 70px;
    text-indent: -99999px;
    background: #000;
    cursor: pointer;
    z-index: 10;
}
#menu3 .side .sideCon .sideHead .close span{
    width: 38px; height: 4px;
    background-color: #fff;
    display: inline-block;
    transform: rotate(-45deg);
    position: absolute;
    top: 32px; left: 16px;
    cursor: pointer;
}
#menu3 .side .sideCon .sideHead .close span:last-child{transform: rotate(45deg);}

/* head_ment */
#menu3 .side .sideCon .sideHead .ment{
    position: absolute;
    width: 100%; height: 180px;
    bottom: 40px; left: 0px;
}
#menu3 .side .sideCon .sideHead .ment .inner{
    position: relative;
    width: 97%; max-width: 1300px;
    margin: 0 auto;
    font-weight: bold;
}
#menu3 .side .sideCon .sideHead .ment .inner .tit::before{
    content: "";
    position: absolute;
    top: -72px; left: 0;
    background: url(../../img/menu4/sp01.png) 0 0 no-repeat;
    width: 50px; height: 50px;
}
#menu3 .side .sideCon .sideHead .ment .inner .tit{
    color: #004e55;
    display: inline-block;
    border-bottom: solid 3px #6ac0a4;
    line-height: 100%;
    position: relative;
    margin-bottom: 20px;
    font-size: 72px;
    font-weight: bolder;
}
#menu3 .side .sideCon .sideHead .ment .inner .tit::after{
    content: "";
    position: absolute;
    bottom: 27px; right: -55px;
    background: url(../../img/menu4/sp01.png) 0 -64px no-repeat;
    width: 43px; height: 50px;
}



/*side03 s*/
#menu3 .sec .boxL {
    display: inline-block; float: left; 
    width: 50%; overflow: hidden; 
    position: relative;    
    background: #555;
    }
#menu3 .sec .boxR {
    display: inline-block; float: right; 
    width: 46%; position: relative; height: 365px;
}
#menu3 .sec:nth-child(2n) .boxL {float: right;}
#menu3 .sec:nth-child(2n) .boxR {float: left;}
#menu3 .sec .boxL img {width: 100%;}
#menu3 .sec .boxR .titBox {padding-top: 25px; position: relative;}
#menu3 .sec .boxR .titBox::after {
    position: absolute; 
    width: 83px; height: 97px; 
    top: -25px; right: 0; 
    background: url("../../img/menu3/sp02.png") 0 0 no-repeat; 
    content: "";
}
#menu3 .sec .boxR .titBox .bottom {
    border: 3px solid;
    padding: 15px; 
    line-height: 140%; 
    background: #6ac0a4;
    font-weight: bold;
}
#menu3 .sec .boxR .txtBox .txt {
    font-family: 'NanumSquare'; font-size: 16px; 
    transform: skew(0.03deg); 
    padding-top: 20px; 
    font-weight: 400;
}
#menu3 .sec .boxR .btnBox {position: absolute; bottom: 20px; width: 100%; }
#menu3 .sec .boxR .btnBox > a { 
    display: inline-block; 
    width: 48%; height: 65px; 
    text-align: center; 
    border: 3px solid; 
    box-sizing: border-box; 
    margin-left: 4%; 
    float: right; 
    position: relative; 
    transition: all .2s ease-in-out; 
    background: #fff;
}
#menu3 .sec .boxR .btnBox > a:hover {background: #f58182;}
#menu3 .sec .boxR .btnBox > a .btnWrap {
    position: absolute; 
    top: 50%; left: 50%; 
    transform: translate(-50%, -50%); 
    width: 100%; box-sizing: border-box;
}

